import * as React from "react"
import { Helmet } from 'react-helmet';
import { Link, graphql } from "gatsby"

import Bio from "../components/bio"
import Layout from "../components/layout"
import Seo from "../components/seo"


const handleOrder = () => {
  // 주문 처리 로직을 여기에 구현하고 성공한 경우 팝업 메시지를 표시합니다.
  // 예시로 간단하게 팝업 메시지를 표시하고자 window.alert를 사용합니다.
  // 실제로는 좀 더 복잡한 팝업 컴포넌트나 라이브러리를 사용하는 것을 권장합니다.
  window.alert('상품이 주문되었습니다.');
};



const BlogIndex = ({ data, location }) => {
  const siteTitle = data.site.siteMetadata?.title || `Title`
  const post = data.allMarkdownRemark.nodes.pop()

 
  return (
    <div>
      <Helmet>
        <script async src="https://www.googletagmanager.com/gtag/js?id=G-33CHNNL3V2"></script>
        <script>
          window.dataLayer = window.dataLayer || [];
          function gtag(){dataLayer.push(arguments)}
          gtag('js', new Date());

          gtag('config', 'G-33CHNNL3V2');
        </script>
      </Helmet>
      <Layout location={location} title={siteTitle}>
        <Bio />

        <div style={{ display: 'flex', justifyContent: 'center' }}>
          {/* 주문하기 버튼 */}
          <button
            onClick={handleOrder}
            className="custom-button"
            style={{
              backgroundColor: 'skyblue',
              fontSize: '5rem',
              padding: '1rem 2rem'
            }}
          >
            주문하기
          </button>
        </div>


        <section
          dangerouslySetInnerHTML={{ __html: post.html }}
          itemProp="articleBody"
        />
      </Layout>
    </div>

    )
  
}

export default BlogIndex

/**
 * Head export to define metadata for the page
 *
 * See: https://www.gatsbyjs.com/docs/reference/built-in-components/gatsby-head/
 */
export const Head = () => <Seo title="All posts" />

export const pageQuery = graphql`
  {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(sort: { frontmatter: { date: DESC } }) {
      nodes {
        html
        excerpt
        fields {
          slug
        }
        frontmatter {
          date(formatString: "MMMM DD, YYYY")
          title
          description
        }
      }
    }
  }
`



